@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500;700&family=Roboto+Mono:wght@100;300;400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{width:100%;display:flex;align-items:center;height:100px}.header a{margin:0 28px;text-decoration:none;color:#181818;font-size:12px;font-weight:600;font-family:'Raleway', sans-serif}.container{position:relative;color:#181818;width:1280px;min-width:1280px;margin:0 auto;display:flex;justify-content:center}.container .page{position:absolute}.container .page .inner{display:flex;justify-content:center;height:90vh}.container .page .inner h1{font-family:'Raleway', sans-serif;font-size:52px;font-weight:800;letter-spacing:0.08rem}.container .page .inner h1 .line-wrap{overflow:hidden;height:66px}.container .page .inner p{font-family:'Roboto Mono', monospace;margin-top:200px;width:340px;font-weight:300;line-height:24px;font-size:14px}.page-enter{opacity:0}.page-enter-active{opacity:1;transition:opacity 400ms;transition-delay:600ms}.page-exit{opacity:1}.page-exit-active{opacity:0;transition:opacity 400ms}

.projects{display:flex;flex-direction:column;align-items:center}.projects h2{margin-bottom:130px;font-size:35px;font-family:'Roboto Mono', monospace}.projects .grid{display:grid;grid-template-columns:repeat(3, 1fr);grid-template-rows:repeat(2, 1fr);grid-gap:30px;gap:30px;justify-content:center;width:90vw;font-family:'Roboto Mono', monospace}.projects .grid .project .image{height:50%;width:100%;object-fit:cover}

